import ErrorReport from '@components/ui/ErrorReport';
import { type LoaderFunctionArgs, redirect } from '@remix-run/node';
import { destroyDataSession, getDataSession } from '@services/cookie.server';
import { destroySession, getSession } from '@services/session.server';

export const loader = async ({ request }: LoaderFunctionArgs) => {
  const headers = new Headers();
  headers.append(
    'Set-Cookie',
    await destroyDataSession(
      await getDataSession(request.headers.get('cookie'))
    )
  );
  headers.append(
    'Set-Cookie',
    await destroySession(await getSession(request.headers.get('cookie')))
  );
  headers.append('Clear-Site-Data', '"storage", "cookies"');
  headers.append('Cache-Control', 'no-store');

  
  headers.append('X-Frame-Options', 'SAMEORIGIN');
  headers.append('Content-Security-Policy', 'frame-ancestors: none');

  return redirect('/login', {
    headers,
  });
};
const Signout = () => <h1>You have been signed out.</h1>;

export const ErrorBoundary = ErrorReport;
export default Signout;
